<template>
  <div class="invite_main">
    <p class="invite_title">{{title}}</p>
    <p class="invite_remark">{{subTitle}}</p>
    <div class="invite_input">
      <div>
        <van-field ref="topText" type="digit" class="invite_top" style="" :maxlength="codeLength"
          v-model="codeValue">
        </van-field>
      </div>
      <!--  :ref="`ref${index}`" @textInput="onTextInput(index, $event)" @input="onInput(index)"
        oninput="value=value.replace(/[^0-9]/g,'')" @keyup.native.delete="onDelete(index)" -->
      <van-field type="digit" class="invite_text" v-for="(item, index) in codeArr.length" disabled
        :key="index" v-model="codeArr[index]" maxlength="1" :class="'len_'+codeArr.length"
        style="width: 3.2rem;">
      </van-field>
    </div>
    <div class="invite_operate">
      <van-button type="primary" class="invite_btn" :loading="invitedLoading" @click="submitCode">
        {{btnName}}
      </van-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "r-digit-input",
  props: {
    title: {
      type: String,
      default: ""
    },
    subTitle: {
      type: String,
      default: ""
    },
    codeLength: {
      type: Number,
      default: 1
    },
    btnName: {
      type: String,
      default: "操作"
    },
    codeName: {
      type: String,
      default: "邀请码"
    }
  },
  data() {
    return {
      invitedLoading: false,
      codeValue: ""
      // codeArr: []
    };
  },
  computed: {
    codeArr() {
      let arr = this.codeValue.split("");
      for (let i = arr.length; i < this.codeLength; i++) {
        arr.push("");
      }
      return arr;
    }
  },
  methods: {
    init() {
      this.codeValue = "";
      setTimeout(() => {
        this.$refs["topText"].focus();
      }, 0);
      // this.codeArr = [];
      // for (let i = 0; i < this.codeLength; i++) {
      //   this.codeArr.push("");
      // }
      // setTimeout(() => {
      //   this.$refs.ref0[0].focus();
      // }, 0);
    },
    /**
     * 输入框替换当前值
     * @param index
     * @param e
     */
    onTextInput(index, e) {
      this.codeArr.splice(index, 1, e.data);
    },
    /**
     * 输入框自动切换
     * @param index
     */
    onInput(index) {
      // index < 5 ，如果是第6格，不触发光标移动至下一个输入框。
      if (this.codeArr[index] && index < this.codeLength - 1) {
        this.$refs["ref" + (index + 1)][0].focus();
      }
    },
    /**
     * 输入框退格自动切换
     * @param index
     */
    onDelete(index) {
      // 如果是第1格，不触发光标移动至上一个输入框
      if (index > 0) {
        this.$refs["ref" + (index - 1)][0].focus();
      }
    },

    submitCode() {
      const index = this.codeArr.find(r => !r);
      if (index > -1) {
        this.$toast("请输入" + this.codeName);
      } else {
        this.$emit("submit", this.codeArr);
      }
    }
  }
};
</script>

<style lang="less" scoped>
/*邀请码弹窗界面*/
.invite_main {
  color: #fff;
  background-color: #00cbab;
  text-align: center;

  .invite_title {
    padding-top: 1rem;
    font-size: 2rem;
    line-height: 2;
  }

  .invite_remark {
    font-size: 1.5rem;
    line-height: 2;
  }

  .invite_input {
    padding: 1.25rem;
    display: flex;
    justify-content: space-evenly;
    .invite_top {
      position: absolute;
      width: 100vw;
      padding: 0.4rem 0;
      margin-left: -2rem;
      background: transparent;
      border: 0;
      z-index: 9;
      /deep/ .van-field__control {
        color: transparent;
      }
    }

    .invite_text {
      font-size: 1.8rem;
      padding: 0.4rem 1rem;
      justify-content: center;
      text-align: center;
    }
    .len_1 {
      margin: 0 calc((100vw - 1 * 3rem) / (2 * 2));
    }
    .len_2 {
      margin: 0 calc((100vw - 2 * 3rem) / (3 * 2));
    }
    .len_3 {
      margin: 0 calc((100vw - 3 * 3rem) / (4 * 2));
    }
    .len_4 {
      margin: 0 calc((100vw - 4 * 3rem) / (5 * 2));
    }
    .len_5 {
      margin: 0 calc((100vw - 5 * 3rem) / (6 * 2));
    }
    .len_6 {
      margin: 0 calc((100vw - 6 * 3rem) / (7 * 2));
    }
  }

  .invite_operate {
    padding: 1.25rem 0;

    .invite_btn {
      background-color: #fff;
      color: #00b79a;
    }
  }
}
</style>



